import { periodTypes } from './period-types';
export const weeklyFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/WEEKLY/));
export const monthlyFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/MONTHLY|QUARTERLY|SIXMONTHLY/));
export const quarterlyFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/QUARTERLY/));
export const sixmonthlyFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/SIXMONTHLY/));
export const monthlyStandardFixedPeriodTypes = ['MONTHLY', 'BIMONTHLY', 'QUARTERLY', 'QUARTERLYJAN', 'SIXMONTHLY', 'SIXMONTHLYJAN'];
export const monthlyOffsetFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/(QUARTERLY|SIXMONTHLY)[A-Z]/));
export const multiMonthFixedPeriodTypes = monthlyFixedPeriodTypes.filter(periodType => periodType !== 'MONTHLY');
export const yearlyFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/^(YEARLY|FY[A-Z]{3})/));
export const financialYearFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/^FY[A-Z]{3}$/));
export const januaryFixedPeriodTypes = [...monthlyStandardFixedPeriodTypes, ...periodTypes.filter(periodType => periodType.match(/JAN$/))];
export const februaryFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/FEB$/));
export const marchFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/MAR$/));
export const aprilFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/APR$/));
export const mayFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/MAY$/));
export const juneFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/JUN$/));
export const julyFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/JUL$/));
export const augustFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/AUG$/));
export const septemberFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/SEP$/));
export const octoberFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/OCT$/));
export const novemberFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/NOV$/));
export const decemberFixedPeriodTypes = periodTypes.filter(periodType => periodType.match(/DEC/));